<template>
  <dl class="s-item">
    <dt>
      <img :src="imageList[Math.floor((Math.random() * 10))]" alt="图片" />
    </dt>
    <dd>
      <h3>
        <router-link :to="{name: 'productDeatil'}">{{meta.title}}</router-link>
      </h3>
      <p>
        <el-rate
          v-model="meta.score"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value}"
        ></el-rate>
        <span v-if="meta.score > 4.5">很好</span>
        <span v-else-if="meta.score > 4">好</span>
        <span v-else-if="meta.score > 3.5">不错</span>
        <span v-else-if="meta.score > 3">一般</span>
        <span v-else>很差</span>
        <span class>{{meta.score}}分</span>
        <!-- <span class="s-item-comment-total">{{meta.commentNum}}人评论</span> -->
      </p>
      <p>
        <span class="s-item-tab">
          <span
            v-for="(v, i) in meta.detailUrl"
            :key="v"
          >{{v + (i < meta.detailUrl.length - 1 ? '|' : '')}}</span>
        </span>
        <span class="s-item-addr">{{meta.areaName}}</span>
        <span>
          <a href="#">
            <i class="el-icon-location"></i>
            查看地图
          </a>
        </span>
      </p>
      <p>
        <span class="s-item-price">人均¥{{meta.avgPrice}}</span>
      </p>
      <ul class="deal-items">
        <li class="items" v-for="(item, index) in detail.slice(0, 2)" :key="index">
          <p class="deal-title">{{item.title}}</p>
          <p>
            <span class="deal-price">¥{{item.price}}</span>
            <span class="deal-old-price">门市价¥{{item.conterPrice}}</span>
            <span class="deal-sales">已售{{item.saleNum}}</span>
          </p>
        </li>
      </ul>
    </dd>
  </dl>
</template>
<script>
export default {
  props: ["meta"],
  data() {
    return {
      detail: [
        {
          title: "火鸡宴，建议10-14人食用",
          price: 909,
          conterPrice: 1150,
          saleNum: 0
        },
        {
          title: "100元代金卷1张",
          price: 28.9,
          contterPrice: 100,
          saleNum: 2305
        }
      ],
      imageList: [
        "http://qcloud.dpfile.com/pc/rh61_6zfmS-xURQOg7c06q8ORnRiISjL_sAVz2Nivs45vVekaqK7wPGea0TMxFwsZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/31rG8bWhlNnTe7TKFvdBic_DxgK_bdKS4j5Mc8JIER6E7IX2h95gFTvWZD-FIxKWZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/P66EijwZIH2NvQvTlPQuUJkZjyAQplRSIN_hOpc4S86zyYoWMmbXx8fLJL81IB4SZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/a1-_QqVyx_m_eyD3qRP0XgjPrLofVq4rU005njdZUxJXAdVXaBgjVI0qTFC99dJdZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/J8n3g1_SSah7ExG4e_N2k2OyzaGC9CvF3Lz9ssgy3BDIuprwJg71lVl7aE9GTJVnZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/grNsNYZlNaPogdO5JAMS1eGBn8bbrUJGcVJrjKlUZMkXXoChnDfITy56gPxPCx0hZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/c5UyA9TgE2bO5xxXfElr08bJblskchIcYqw5wU3-Cno0azOZPWVj-iRCLwhZ33-KZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/qeF_7cT1m3fCLcmvnI6mnE6TrLJStzW24bq-ZJ49MMrs8dgbgpmITntVzwQFQKX3ZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/yvYxafvWTXu94y4NKHVBkLRnDW-TNYhU-HiOLrjen0qU4u6hY-0mYCR_UMRvTUqaZSUjBikR5Ecy-DoGYkMhlg.jpg",
        "http://qcloud.dpfile.com/pc/3uh89jA0lt5-IWiTXiUyBB5kjSslKdBnaCubqpLbgs2c_KUldn9IAxRtkEd5MYR0ZSUjBikR5Ecy-DoGYkMhlg.jpg"
      ]
    };
  },
  created() {
    // console.log(this.meta);
  }
};
</script>


